import { useEffect, useState } from "react";

export default function Header() {
  const [color, toggleColor] = useState("span light-2");
  const [color2, toggleColor2] = useState("span light-1");

  const [color3, toggleColor3] = useState("span light-1");

  setInterval(() => {
    if (color.includes("light-1")) {
      toggleColor("span light-2");
    } else {
      toggleColor("span light-1");
    }
  }, 3000);
  setInterval(() => {
    if (color2.includes("light-1")) {
      toggleColor2("span light-2");
    } else {
      toggleColor2("span light-1");
    }
  }, 6000);
  setInterval(() => {
    if (color3.includes("light-1")) {
      toggleColor3("span light-2");
    } else {
      toggleColor3("span light-1");
    }
  }, 9000);

  return (
    <div className="header-container">
      <div className="bulb-container">
        <div className={color2}></div>
        <div className={color}></div>
        <div className={color3}></div>
      </div>
    </div>
  );
}
